.profile-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;

  &__container {
    margin-left: 1rem;
  }

  &__name {
    color: var(--pale-violet);
    font-size: 1rem;
    font-weight: 600;
  }

  &__tag {
    font-size: 0.75rem;
    font-weight: 400;
    cursor: pointer;
  }
}
